import raf from "./raf";

/**
* requestAnimationFrame 
* scroll  touchmove 
*  requestAnimationFrame 
*/
export default function requestAnimationFrameThrottle(callback) {
	let id;

	const factory = args => () => {
		id = null;
		callback(...args);
	};

	const throttled = (...args) => {
		if (id == null) {
			id = raf(factory(args));
		}
	};

	throttled.cancel = () => raf.cancel(id);

	return throttled;
};


